section{
    position: relative;
    overflow: visible;

    .section-content-wrapper{
        width: 100%;
        padding: 100px 15px;

        .section-text{
            @include flex-center-top;
            width: 100%;
        }
        .section-content{
            width: auto;
            flex-wrap: wrap;
            position: relative;
        }
    }
}

hr{
    margin: 35px;
}

.black-text{
    color: black;
}

