.logos-container{
    @include flex-top-center-column;
    margin: 12px;
    position: relative;

    // border-radius: 4px;
    // padding: 5px;
    // background-color: white;
    // box-shadow:0 0 5px rgba(0,0,0,0.8);
    // height: auto;
    .skills-logo{
        height: 50px;
        margin: 15px;
    }
    .skill-name{
        // position: absolute;
        // bottom: -15px;
        color: black;
        text-align: center;
        margin: 0 auto;
    }
}

//Animate Skills on Enter/Leave
.logos-container.ng-enter{
    transition: 0.3s ease-out all;
    opacity: 0;
    transform: translateX(50px);
}
.logos-container.ng-enter.ng-enter-active{
    opacity: 1;
    transform: translateX(0px);
}
.logos-container.ng-enter-stagger{
    transition-delay: 0.03s;
    transition-duration: 0;
}








.project-card{
    width: 250px;
    height: 450px;
    border-radius: $border-radius-card;
    box-shadow:0 0 20px rgba(0,0,0,0.8);
    // margin-right: 66px;
    overflow: hidden;
    position: relative;
    
    .project-thumbnail{
        width: 100%;
        height: 100%;
        // background: url('../images/backgrounds/gradient_white_background.png') no-repeat center center;
        background: black;
        background-size: cover;
    }
    .card-info-wrapper{
        overflow: hidden;
        z-index: 1;
        height: 100%;
        position: relative;
        .card-info{
            padding: 0 10px;
            position: relative;
            top: 76.5%;
            text-align: center;
            h4{
                color: $brand-black;
            }
            p{
                color: $brand-black;
                
            }
        }
    }
    .card-info-wrapper::before{
        content: '';
        width: 100%;
        height: 32%;
        background: lightgray;
        position: absolute;
        bottom: -9px;
        left: 0;
        transform: skewY(-4.2deg);
        
    }
}


.card-wrapper{
    @include flex-center-center;
    overflow: visible;
    
}


